.App {
  text-align: center;
  height: 100vh;
  background-color: whitesmoke;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}
.mainapp {
  display: flex;
  background-color: whitesmoke;
  justify-content: center;
  height: 80vh;
  margin: 0;
  align-items: center;
  flex-direction: column;
}
